<!--scopedcssscopedcss-->
<!---->
<style scoped>
.el-card{
  margin-top: 15px;
  font-size: 12px;
}
.el-.el-table td div {
  font-size: 12px;
}
.el-table__body tr .el-button, .el-table__body td .el-button{
  width: 40px;
}
</style>
