<template>
  <div class="store">
    <el-container>
      <!--  -->
      <el-header>
        <div class="left">
          <img src="../assets/logo.png" alt="" />
          <p></p>
        </div>
        <el-button type="info" @click="out"></el-button>
      </el-header>
      <el-container>
        <!--  -->
        <el-aside width="">
          <p @click="isCollapse=!isCollapse">|||</p>
          <el-menu
          unique-opened
          
            router
            text-color="#fff"
            background-color=" #333744"
            :default-active="xb"
            class="el-menu-vertical-demo"
            @open="handleOpen"
            @close="handleClose"
            :collapse="isCollapse"
          >
            <el-submenu :index="item.id+''" :key="index" v-for="(item,index) in list">
              <template slot="title">
                <i :class="icon[index]"></i>
                <span slot="title">{{item.authName}}</span>
              </template>
              <el-menu-item-group :key="i" v-for="(child,i) in item.children">
                <el-menu-item :index="'/store/'+child.path" @click="jump('/store/'+child.path)"><i class="el-icon-menu"></i>{{child.authName}}</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </el-aside>
        <!--  -->
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  // 
  name: "demo",
  //  
  props: {},
  // 
  components: {},
  // 
  data() {
    return {
      isCollapse: false,
      // 
      icon:["el-icon-user-solid","el-icon-s-help","el-icon-s-goods","el-icon-s-claim","el-icon-s-marketing"],
      // 
      list:[],
      // 
      xb:sessionStorage.getItem("index")||''
    };
  },
  // 
  computed: {},
  // 
  watch: {
    // 
    $route(to,from){
      if(to.path=='/store/sy'){
        this.xb=''
      }else{
        this.xb = to.fullPath+''
      }
      sessionStorage.setItem("index",to.fullPath+'')
    }
  },
  // 
  methods: {
    // 
    out() {

      this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          sessionStorage.removeItem("token");
      this.$router.push("/login");
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
      
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    // 
    async getList(){
      let {data} = await this.$http.get("menus")
      this.list = data.data
      console.log(this.list)
    },
    // 
    jump(i){
      console.log(i)
      sessionStorage.setItem("index",i+'')
      this.xb=i
    }
  },
  mounted(){
    this.getList()
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--scopedstyle-->
<!--scopedcssscopedcss-->
<!---->
<style lang="scss" scoped>
.store,
.el-container {
  height: 100%;
}
// 
.el-header {
  display: flex;
  align-items: center;
  background-color: #373d41;
  justify-content: space-between;
  line-height: 60px;
  padding: 0 20px;
  box-sizing: border-box;
  flex-shrink: 0;
}

.el-aside {
  background-color: #333744;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
}

// 
.left {
  display: flex;
  align-items: center;
  img {
    margin-right: 15px;
    width: 50px;
  }
  p {
    font-size: 22px;
    color: #fff;
  }
}

// 
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
p{
  background-color: #4a5064;
    color: #fff;
    font-size: 12px;
    text-align: center;
    line-height: 22px;
    cursor: pointer;
    letter-spacing: .12em;
}
</style>
