<template>
  <div class="users">
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="10">
          <el-input
            v-model="query"
            placeholder=""
            class="input-with-select"
          >
            <el-button
              @click="getUser()"
              slot="append"
              icon="el-icon-search"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="6">
          <el-button
            type="primary"
            size="mini"
            @click="dialogFormVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>
      <!--  -->
      <el-table border :data="users" stripe style="width: 100%">
        <el-table-column type="index" label="#" width=""> </el-table-column>
        <el-table-column prop="username" label="" width="180">
        </el-table-column>
        <el-table-column prop="email" label="" width="180">
        </el-table-column>
        <el-table-column prop="mobile" label="" width="180">
        </el-table-column>
        <el-table-column prop="role_name" label="" width="180">
        </el-table-column>
        <el-table-column prop="mg_state" label="" width="180">
          <template slot-scope="scope">
            <!--  -->
            <el-switch
              @change="change(scope.row.id, scope.row.mg_state)"
              v-model="scope.row.mg_state"
              active-color="#409EFF"
              inactive-color="#DCDFE6"
            >
            </el-switch>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column label="" prop="id" width="180">
          <template slot-scope="scope">
            <!--  -->
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="update(scope.row)"
              
            ></el-button>
            <!--  -->
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="delUser(scope.row.id)"
            ></el-button>
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top-start"
            >
              <el-button
                size="mini"
                type="warning"
                icon="el-icon-setting"
                @click="role(scope.row.id)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          height="50"
          :current-page="pagenum"
          :page-sizes="[2, 4, 6, 10]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <!--  -->
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="ruleForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="ruleForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="ruleForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="ruleForm.phone"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <div slot="footer" class="dialog-footer">
        <!-- ref!!! -->
        <el-button @click="resetForm('ruleForm')"> </el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisibleUpdate">
      <!--  -->
      <el-form
        :model="ruleFormUpdate"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="">
          <el-input v-model="ruleFormUpdate.name" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="ruleFormUpdate.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="ruleFormUpdate.phone"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleUpdate = false"> </el-button>
        <el-button type="primary" @click="updateForm('ruleForm')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  // 
  name: "demo",
  //  
  props: {},
  // 
  components: {},
  // 
  data() {
    // 
    let isPhone = (rule, value, callback) => {
      if (value === "") {
        return callback(new Error(""));
      } else if (/^1[3456789]\d{9}$/.test(value)) {
        return callback();
      } else {
        return callback("");
      }
    };
    return {
      // 
      search: "",
      users: [], //
      pagesize: 2,
      pagenum: 1,
      query: "", //
      total: 0,
      //   
      dialogFormVisible: false,
      dialogFormVisibleUpdate: false,
      formLabelWidth: "100px",
      // 
      ruleForm: {
        username: "",
        password: "",
        email: "",
        phone: "",
      },
      // 
      ruleFormUpdate: {
        name: "",
        email: "",
        phone: "",
        id: "",
      },
      // 
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" },
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          // { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        email: [
          { required: true, message: "", trigger: "blur" },
          {
            type: "email",
            message: "",
            trigger: ["blur", "change"],
          },
        ],
        phone: [
          // { required: true, message: "", trigger: "blur" },
          {required: true, validator: isPhone, trigger: "blur" },
        ],
      },
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {
    // 
    // 
    async getUser(num = 1, size = 2) {
      let { data } = await this.$http.get(
        `users?query=${this.query}&pagenum=${num}&pagesize=${size}`
      );
      this.users = data.data.users;
      this.pagesize = size;
      this.pagenum = num;
      this.total = data.data.total;
      console.log(data.data);
    },
    // 
    async addUser() {
      // await this.submitForm('ruleForm')
      let add = await this.$http.post("users", {
        username: this.ruleForm.username,
        password: this.ruleForm.password,
        email: this.ruleForm.email,
        mobile: this.ruleForm.phone,
      });
      console.log(add);
      if (add.data.meta.status == 201) {
        this.$message({
          message: "",
          type: "success",
        });
      // 
        this.ruleForm = { username: "", password: "", email: "", phone: "" };
        this.dialogFormVisible = false;
      } else {
        this.$message({
          message: "",
          type: "warning",
        });
      }
      // // 
      this.getUser(1, 2);
    },
    // 
   submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.addUser()
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      // 
   updateForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.updated()
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      // 
      resetForm(formName) {
        this.dialogFormVisible = false
        this.$refs[formName].resetFields();
      },
    // 
    update(i) {
      this.dialogFormVisibleUpdate = true;
      this.ruleFormUpdate.name = i.username;
      this.ruleFormUpdate.email = i.email;
      this.ruleFormUpdate.phone = i.mobile;
      this.ruleFormUpdate.id = i.id;
      console.log(this.ruleFormUpdate);
    },
    // 
    async updated() {
      let { data } = await this.$http.put("users/" + this.ruleFormUpdate.id, {
        id: this.ruleFormUpdate.id,
        email: this.ruleFormUpdate.email,
        mobile: this.ruleFormUpdate.phone,
      });
      if (data.meta.status == 200) {
        this.$message({
          message: "",
          type: "success",
        });
      }
      console.log(data.data.id);
      this.dialogFormVisibleUpdate = false;
      this.getUser()
    },
    // 
    delUser(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          console.log(111);
          this.$http.delete("users/" + id).then((res) => {
            console.log(res);
            this.getUser()
          });
          this.$message({
            type: "success",
            message: "!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
        
    },
    // 
    role(i) {
      alert("");
    },
    // 
    // num
    handleSizeChange(size, num = this.pagenum) {
      this.getUser(num, size);
    },
    // size
    handleCurrentChange(num, size = this.pagesize) {
      this.getUser(num, size);
    },
    // 
    async change(id, flag) {
      console.log(id, flag);
      let { data } = await this.$http.put(`users/${id}/state/${flag}`);
      console.log(data);
      if (data.meta.status == 200) {
        this.$message({
          message: "",
          type: "success",
        });
      }
    },
  },
  mounted() {
    this.getUser(1, 2);
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--scopedstyle-->
<!--scopedcssscopedcss-->
<!---->
<style lang="scss" scoped>
/*  */
.box-card {
  margin-top: 15px;
}
.el-col {
  display: flex;
  height: 50px;
  .el-button {
    height: 40px;
  }
}
// ()
.el-table__header tr,
.el-table__header th {
  padding: 0;
  height: 30px;
  line-height: 30px;
}
.el-table__body tr,
.el-table__body td {
  padding: 4px 0;
  // height: 30px;
  // line-height: 30px;
  .el-button {
    width: 46px;
    padding: 10px;
    height: 30px;
    .i {
      height: 80%;
      top: 0;
    }
  }
}
.el-table {
  border-radius: 5px;
}

// 
.el-pagination {
  height: 20px;
  margin-top: 15px;
  .el-pager {
    height: 100%;
  }
}
</style>
