<template>
<div class="login">
    <div class="card">
        <div class="img">
            <img src="../assets/logo.png" alt="">
        </div>
        <!--  -->
        <el-form status-icon label-width="100px" class="demo-ruleForm">
        <div class="demo-input-suffix">
            <el-input
           
            prefix-icon="el-icon-user-solid"
            v-model="username">
            </el-input>
        </div>
  <div class="demo-input-suffix">
            <el-input
           type="password"
            prefix-icon="el-icon-lock"
            v-model="password">
            </el-input>
        </div>
 <el-row>
  <el-button type="primary" @click="getToken"></el-button>
  <el-button type="info" @click="reset"></el-button>
</el-row>
</el-form>
    </div>
</div>
</template>

<script>
export default {
  // 
  name: 'demo',
  //  
  props: {},
  // 
  components: {},
  // 
  data () {
   return {
       username:"admin",//
       password:"123456"//
   }
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {
    //   
    async getToken(){
        let {data} = await this.$http.post("login",{
            username:this.username,
            password:this.password
        })
        // console.log(data)
        if(data.meta.status==200){
           sessionStorage.setItem("token",''+data.data.token)
        this.$router.push('/store')
        // 
        this.$message({
          message: '',
          type: 'success'
        });
        }else{
           this.$message.error('');
        this.reset()
        }
        
    },
    // 
    reset(){
         this.username="admin",//
       this.password="123456"//
    }
  },
}
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--scopedstyle-->
<!--scopedcssscopedcss-->
<!---->
<style  lang="scss" scoped>
.login{
    background-color: #2b4b6b;
    height: 100%;
}
// 
.card{
    background-color: #fff;
    position: absolute;
    left: 50%;
    transform: translate(-50%,-50%);
    box-shadow: 0 0 10px #ddd;
width: 450px;
    height: 304px;
    border-radius: 3px;
    top: 50%;
    // 
    .img{
            width: 130px;
    height: 130px;
    padding: 10px;
    border: 1px solid #efefef;
    border-radius: 50%;
    background-color: #fff;
    position: absolute;
    left: 50%;
    transform: translate(-50%,-50%);
    box-shadow: 0 0 10px #ddd;
    img{
        width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #efefef;
    }
    }
}

// 
.el-form{
    position: absolute;
    bottom: 20px;
    left: 0;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
    .el-input{
        margin-bottom: 20px;
    }
    .el-row{
        text-align: right;
        margin-top: 10px;
    }
}
</style>
