<template>
  <div class="">
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--   -->
      <el-row>
        <el-button type="primary" @click="add"></el-button>
      </el-row>
      <!--  -->
      <el-table
        :data="list"
        style="width: 100%;margin-bottom: 20px;"
        row-key="cat_id"
        border
        :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
      >
        <el-table-column type="index" label="#" width=""> </el-table-column>
        <el-table-column prop="cat_name" label="" width="180">
        </el-table-column>
        <el-table-column prop="cat_deleted" label="" width="">
          <template slot-scope="scope">
            <i class="el-icon-success" style="color:#67c23a;"></i>
          </template>
        </el-table-column>
        <el-table-column width="" label="">
          <template slot-scope="scope">
            <div
              style="background:#409eff;"
              class="hmw"
              v-if="scope.row.cat_level == 0"
            >
              
            </div>
            <div
              style="background:#67c23a;"
              class="hmw"
              v-if="scope.row.cat_level == 1"
            >
              
            </div>
            <div
              style="background:#e6a23c;"
              class="hmw"
              v-if="scope.row.cat_level == 2"
            >
              
            </div>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column label="" prop="cat_id" width="180">
          <template slot-scope="scope">
            <!--  -->
            <el-button type="primary" icon="el-icon-edit" @click="update(scope.row)"></el-button>
            <!--  -->
            <el-button type="danger" icon="el-icon-delete" @click="del(scope.row.cat_id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          height="50"
          :current-page="pagenum"
          :page-sizes="[5, 4, 6, 10]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <!--  -->
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>

        <el-form-item label="">
          <div class="block">
            <el-cascader
            v-model="ruleForm.value"
            collapse-tags
              :options="list2"
              :props="{ checkStrictly: true,value:'cat_id',label:'cat_name'}"
              clearable
            ></el-cascader>
          </div>
        </el-form-item>
      </el-form>
      <!--  -->
      <div slot="footer" class="dialog-footer">
        <!-- ref!!! -->
        <el-button @click="resetForm('ruleForm')"> </el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"
          > </el-button
        >
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisibleUpdate">
      <!--  -->
      <el-form
        :model="ruleFormUpdate"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="ruleFormUpdate.name"></el-input>
        </el-form-item>
       
      </el-form>
      <!--  -->
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleUpdate = false"> </el-button>
        <el-button type="primary" @click="updated('ruleForm')"> </el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
export default {
  // 
  name: "demo",
  //  
  props: {},
  // 
  components: {},
  // 
  data() {
    return {
      list: [],
      
      pagesize: 5,
      pagenum: 1,
      type: "", //
      total: 0,
      //  
      dialogFormVisible: false,
      // 
      dialogFormVisibleUpdate: false,
      formLabelWidth: "100px",
      // 
      ruleForm: {
        value:[],
        name: ""
      },
      // 
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      // 
      addList: [],
     // 
      ruleFormUpdate: {
        name: "",
        id:0
      },
      // 
      list2:[]
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {
    // 
    async getList(num = 1, size = 5) {
      let { data } = await this.$http.get(
        `categories?type=${this.type}&pagenum=${num}&pagesize=${size}`
      );
      // console.log(data)
      this.list = data.data.result;
      this.total = data.data.total;
      this.pagenum = data.data.pagenum;
      this.pagesize = data.data.pagesize;
      console.log(this.list, this.total);
    },
    // 
    // num
    handleSizeChange(size, num = this.pagenum) {
      this.getList(num, size);
    },
    // size
    handleCurrentChange(num, size = this.pagesize) {
      this.getList(num, size);
    },
    // 
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.submitAdd()
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // 
    async submitAdd(){
      console.log(111)
        let data = await this.$http.post("categories",{
          cat_pid :this.ruleForm.value.length+1,
          cat_name:this.ruleForm.name,
          cat_level:this.ruleForm.value.length
        })
        console.log(data)
        console.log(this.ruleForm.value.length)
        this.dialogFormVisible = false;
        this.getList()
    }
    ,
    // 
    resetForm(formName) {
      this.dialogFormVisible = false;
      this.$refs[formName].resetFields();
    },
    // 
    async add() {
      this.getList2()
      this.dialogFormVisible = true;
    },
  // 
  del(id){
    // 
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          console.log(id);
          this.$http.delete("categories/" + id).then((res) => {
            console.log(res);
            // 
             this.getList()
          });
          this.$message({
            type: "success",
            message: "!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
  },
   // 
    update(i) {
      this.dialogFormVisibleUpdate = true;
      this.ruleFormUpdate.name = i.cat_name;
      this.ruleFormUpdate.id = i.cat_id;
      console.log(this.ruleFormUpdate);
    },
    // 
    async updated() {
      let { data } = await this.$http.put("categories/" + this.ruleFormUpdate.id, {
        cat_name: this.ruleFormUpdate.name,
      });
      if (data.meta.status == 200) {
        this.$message({
          message: "",
          type: "success",
        });
      }
      console.log(data.data);
      this.dialogFormVisibleUpdate = false;
      this.getList()
    },
  // 
   async getList2(num = 1, size = this.total) {
      let { data } = await this.$http.get(
        `categories?type=${this.type}&pagenum=${num}&pagesize=${size}`
      );
       data.data.result.map((i)=>{
        if(i.children){
          i.children.map((is)=>{
            delete is.children
          })
        }
      })
      console.log(data.data.result)
      this.list2 =data.data.result
      console.log(this.list2)
    },
  },
  mounted() {
    this.getList();
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--scopedstyle-->
<!--scopedcssscopedcss-->
<!---->
<style lang="scss">
.el-card {
  margin-top: 15px;
}
/*  */

.el-table {
  margin-top: 15px;
}
.el-table__body tr .el-button,
.el-table__body td .el-button {
  width: 70px;
  padding: 7px;
  /* font-size: 12px !important; */
}
/*  */
.el-table td .hmw {
  width: 38px;
  height: 20px;
  color: white;
  border-radius: 5px;
  font-size: 12px;
  text-align: center;
}
.el-cascader{
  width: 100%;
}
</style>
