import vueTemplateCompiler from 'vue-template-compiler';
<template>
<div class="">
     <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
       <!--  -->
      <el-row :gutter="20">
        <el-col :span="10">
          <el-input
          v-model="query"
            placeholder=""
            class="input-with-select"
          >
            <el-button
            @click="getList()"
              slot="append"
              icon="el-icon-search"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="6">
          <el-button
            type="primary"
            @click="add"
            ></el-button
          >
        </el-col>
      </el-row>
      <!--  -->
      <el-table border :data="list" stripe style="width: 100%">
        <el-table-column type="index" label="#" width="50"> </el-table-column>
        <el-table-column prop="goods_name" label="" width="700">
        </el-table-column>
        <el-table-column prop="goods_price" label="()" width="110">
        </el-table-column>
        <el-table-column prop="goods_weight" label="" width="80">
        </el-table-column>
        <el-table-column prop="upd_time" label="" width="180">
          <template slot-scope="scope">
           {{scope.row.upd_time|timefn}}
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column label="" width="120">
          <template slot-scope="scope">
            <!--  -->
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
            ></el-button>
            <!--  -->
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="del(scope.row.goods_id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          height="50"
          :current-page="pagenum"
          :page-sizes="[5, 20, 30, 10]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
        </div>
    </el-card>
</div>
</template>

<script>
export default {
  // 
  name: 'demo',
  //  
  props: {},
  // 
  components: {},
  // 
  data () {
   return {
     list:[],
    //  
     dialogFormVisible: false,
     formLabelWidth: "100px",
      // 
      ruleForm: {
        name: "",
      },
       // 
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
        ]
      },
      // 
      query:'',
      pagenum:1,
      pagesize:10,
      total:0,
      // 

   }
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {
    // 
    async getList(num=this.pagenum,size=this.pagesize){
      let {data} = await this.$http.get(`goods?query=${this.query}&pagenum=${num}&pagesize=${size}`)
      console.log(data.data)
      this.list=data.data.goods
      this.total=data.data.total
      this.pagesize = size;
      this.pagenum = num;
      console.log(this.total,this.list)
    },
      // 
      add(){
        this.dialogFormVisible = true
      },
      // 
    // num
    handleSizeChange(size, num = this.pagenum) {
      this.getList(num, size);
    },
    // size
    handleCurrentChange(num, size = this.pagesize) {
      this.getList(num, size);
    },
  // 
   // 
    del(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          console.log(id)
          this.$http.delete("goods/" + id).then((res) => {
            console.log(res);
            this.getList()
          });
          
          this.$message({
            type: "success",
            message: "!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
  },
  mounted(){
    this.getList()
  }
}
</script> 

